import React from 'react';
import Label from '../../atoms/label/Label';
import './TextBox.css';

interface TextBoxProps {
  
  /**
   * className 설정
   */
  className?: string[];

  /**
   * style 정의
   */
  style?: object;

}

export const TextBox = ({ style, className
}: TextBoxProps) => (
  <div
    style={style}
    className={`${className.join(' ')}`}
  >
    <Label text="Atlassian Documentation"/>
  </div>
);

/**
 * react/require-default-props
 */
TextBox.defaultProps = {
  style: {},
  className: [],
};

export default TextBox;
